.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35;
}
.sidebar_header{
    display:  flex;
    justify-content: space-around;
    padding: 20px;
    border-right: 1px solid lightgray;
}
.headerRight{
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 10vw;
}

.siderserch{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 40px;
    padding: 10px;
}
.siderbarsearch_container {
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px; 
  }
  .siderbarsearch_container > input {
    border: none;
    outline-width: 0;
  }
  /* sidebar chat */
  .sideBarChat{
    display: flex;
    padding: 10px;
    cursor: pointer;
    border-bottom: 1px solid #f6f6f6;
  }
  .sideBarChat:hover{
     background-color: #d3d0d0;
  }
  .sidebarchat_info >h4{
    font-size: 1rem;
    margin-bottom: 8px;
    /* margin-top: -0.2rem; */
  }
  .sidebarchat_info >p{
    font-size: 1rem;
  }
  .sidebarchat_info{
    margin-left: 1.5rem !important;
  }
  